@media only screen and (max-width: 767px) and (min-width: 320px) {
    .story-icon,
    .story-top,
    .story-bottom,
    .story .content-wrapper::before {
        left: 35px
    }

    .story .item {
        width: 100%
    }

    .story .item .story-content,
    .story .item:nth-child(even) .story-content {
        width: calc(100vw - 110px);
        margin-left: 80px;
    }

    .story .story-content h2 {
        font-size: 34px;
    }

    .lavelo-title h2,
    .contact-title h2 {
